import { Injectable } from '@angular/core';

import { Http } from '@angular/http';

import { UserData } from './user-data';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/of';


@Injectable()
export class ConferenceData {
  data: any;

  constructor(public http: Http, public user: UserData) { }

  load(): any {
    if (this.data) {
      return Observable.of(this.data);
    } else {
      return this.http.get('assets/data/data.json')
        .map(this.processData, this);
    }
  }

  processData(data: any) {
    // just some good 'ol JS fun with objects and arrays
    // build up the data by linking instructors to courses
    this.data = data.json();

    this.data.tracks = [];

    // loop through each day in the schedule
    this.data.schedule.forEach((day: any) => {
      // loop through each timeline group in the day
      day.groups.forEach((group: any) => {
        // loop through each course in the timeline group
        group.courses.forEach((course: any) => {
          course.instructors = [];
          if (course.instructorNames) {
            course.instructorNames.forEach((instructorName: any) => {
              let instructor = this.data.instructors.find((s: any) => s.name === instructorName);
              if (instructor) {
                course.instructors.push(instructor);
                instructor.courses = instructor.courses || [];
                instructor.courses.push(course);
              }
            });
          }

          if (course.tracks) {
            course.tracks.forEach((track: any) => {
              if (this.data.tracks.indexOf(track) < 0) {
                this.data.tracks.push(track);
              }
            });
          }
        });
      });
    });

    return this.data;
  }

  getTimeline(dayIndex: number, queryText = '', excludeTracks: any[] = [], segment = 'all') {
    return this.load().map((data: any) => {
      let day = data.schedule[dayIndex];
      day.shownCourses = 0;

      queryText = queryText.toLowerCase().replace(/,|\.|-/g, ' ');
      let queryWords = queryText.split(' ').filter(w => !!w.trim().length);

      day.groups.forEach((group: any) => {
        group.hide = true;

        group.courses.forEach((course: any) => {
          // check if this course should show or not
          this.filterCourse(course, queryWords, excludeTracks, segment);

          if (!course.hide) {
            // if this course is not hidden then this group should show
            group.hide = false;
            day.shownCourses++;
          }
        });

      });

      return day;
    });
  }

  filterCourse(course: any, queryWords: string[], excludeTracks: any[], segment: string) {

    let matchesQueryText = false;
    if (queryWords.length) {
      // of any query word is in the course name than it passes the query test
      queryWords.forEach((queryWord: string) => {
        if (course.name.toLowerCase().indexOf(queryWord) > -1) {
          matchesQueryText = true;
        }
      });
    } else {
      // if there are no query words then this course passes the query test
      matchesQueryText = true;
    }

    // if any of the courses tracks are not in the
    // exclude tracks then this course passes the track test
    let matchesTracks = false;
    course.tracks.forEach((trackName: string) => {
      if (excludeTracks.indexOf(trackName) === -1) {
        matchesTracks = true;
      }
    });

    // if the segement is 'favorites', but course is not a user favorite
    // then this course does not pass the segment test
    let matchesSegment = false;
    if (segment === 'favorites') {
      if (this.user.hasFavorite(course.name)) {
        matchesSegment = true;
      }
    } else {
      matchesSegment = true;
    }

    // all tests must be true if it should not be hidden
    course.hide = !(matchesQueryText && matchesTracks && matchesSegment);
  }

  getInstructors() {
    return this.load().map((data: any) => {
      return data.instructors.sort((a: any, b: any) => {
        let aName = a.name.split(' ').pop();
        let bName = b.name.split(' ').pop();
        return aName.localeCompare(bName);
      });
    });
  }

  getTracks() {
    return this.load().map((data: any) => {
      return data.tracks.sort();
    });
  }

  getMap() {
    return this.load().map((data: any) => {
      return data.map;
    });
  }

}



