import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import 'rxjs/add/operator/map';

import { Observable } from 'rxjs/Observable';

import { Apollo } from 'apollo-angular';  
import gql from 'graphql-tag';

const mutationSignupUser = gql`
  mutation (
    $email: String!,
    $password: String!,
    $username: String!,
    $firstName: String!,
    $lastName: String!
  ) {
    signupUser(
      email: $email,
      password: $password,
      username: $imageUrl,
      firstName: $firstName,
      lastName: $lastName
    ) {
      id
      token
    }
  }
`;

const mutationAuthenticateUser = gql`
  mutation (
    $email: String!,
    $password: String!
  ) {
    authenticateUser(
      email: $email,
      password: $password
    ) {
      id
      token
    }
  }
`;

const queryIsLoggedIn = gql`
  query {
    loggedInUser {
      id
    }
  }
`;

@Injectable()
export class AuthProvider {
  public token: any;
  public currentUser: any;
	api = 'https://edumate-server.herokuapp.com/api';
  constructor(
    public http: Http,
    public events: Events,
    public storage: Storage,
    private apollo: Apollo
  ) {
    // proxy for development
    this.api = '/api';
  }

  checkAuthentication() {
    return new Promise((resolve, reject) => {
      // Load token if exists
      this.storage.get('data')
      .then(value => {
        if (value) {
          this.token = value.token;
          this.currentUser = value.user;
        }
        // let headers = new Headers();
        // headers.append('Authorization', this.token);
        // this.http.get(this.api + '/auth/protected', {headers: headers})
        // .subscribe(res => {
        //   console.log(res);
        //   this.events.publish('user:reauth');
        //   resolve(res);
        // }, (err) => {
        //   reject(err);
        // });
        console.log("checking auth");
        const queryWatcher = this.apollo.watchQuery<any>({
          query: queryIsLoggedIn
        });
    
        return queryWatcher.valueChanges
          .map(result => resolve(result.data));

        // console.log('reauthed with data:');
        // console.log(res);
        // this.events.publish('user:reauth');
      });
    });
  }

  setCurrentUser(user) {
    this.currentUser = user;
    var data = {
      token: this.token,
      user: user
    }
    this.storage.set('data', data);
  }
 
  createAccount(details) {
		return new Promise((resolve, reject) => {
      this.apollo.mutate({
        mutation: mutationSignupUser,
        variables: {
          email: details.email,
          password: details.password,
          username: details.username,
          firstName: details.firstName,
          lastName: details.lastName
        }
      }).subscribe(res => {
        // let data = res.json();
        // console.log('registered with data:');
        // console.log(data);
        // this.storage.set('data', data);
        // this.token = data.token;
        // this.currentUser = data.user;
        this.events.publish('user:register');
      }, err => {
        reject(err);
      })
		});
  }
 
  login(credentials) {
    return new Promise((resolve, reject) => {
      this.apollo.mutate({
        mutation: mutationAuthenticateUser,
        variables: {
          email: credentials.email,
          password: credentials.password
        }
      }).subscribe(result => {
        // let data = res.json();
        console.log('logged in with data:');
        console.log(result.data);
        // this.storage.set('data', data);
        // this.token = data.token;
        // this.currentUser = data.user;
        // this.events.publish('user:login');
        // resolve(res);
      }, err => {
        reject(err);
      });
    });
  }
 
  logout() {
    this.token = null;
    this.currentUser = null;
    this.events.publish('user:logout');
    return this.storage.clear();
  }
}


